.header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 9;
    z-index: (--z-fixed);
    background-color: var(--body-color);
}

.nav {
    height: calc(var(--header-height) + 1.5rem);
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 1rem;

    .nav__list {
        display: flex;
        column-gap: 2rem;
    } 
    
    .nav__icon{
        display: none;
    }
}

.nav__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: var(--small-font-size);
    color: var(--title-color);
    font-weight: var(--font-medium);
    transition: .3s;

    &.active {
        color: var(--title-color-dark);
    }

    
    &:hover{
        color: var(--title-color-dark)
    }
}


.nav__logo,
.nav__toggle {
    color: var(--title-color);
    font-weight: var(--font-medium);
}

.nav__close,
.nav__toggle{
    display: none;
}

// =================== BREAKPOINT =================

@media screen and (max-width: 768px) {
    .header {
        top: initial;
        bottom: 0;
    }

    .nav{
        height: var(--header-height);

        .nav__icon{
            display: block;
            font-size: 1.2rem;
        }

        .nav__list {
            display: grid;
            grid-template-columns: repeat(3 , 1fr);
            gap: 2rem;
        }
    } 

    // SHOW-MENU 

    .nav__menu {
        position: fixed;
        bottom: -100%;
        left: 0;
        width: 100%;
        background-color: var(--body-color);
        padding: 2rem 1.5rem 4rem;
        box-shadow: 0 -1px 4px var(--box-shadow);
        border-radius: 1.5rem 1.5rem 0 0;
        transition: .3s;

    }

    .nav__close{
        position: absolute;
        right: 1.3rem;
        bottom: .5rem;
        font-size: 1.5rem;
        cursor: pointer;
        color: var(--title-color);

        &:hover {
            color: var(--title-color-dark);
        }
    }

    .show-menu {
        bottom: 0;
    }

    .nav__toggle{
        font-size: 1.1rem;
        cursor: pointer;
    }

    .nav__close,
    .nav__toggle {
        display: block;
    }
}